
"use client";

import React, { createContext, useContext, useMemo, ReactNode } from 'react';
import type { Employee } from '@/lib/types';
import { useUser as useFirebaseAuthUser } from '@/firebase';
import { doc } from 'firebase/firestore';
import { useFirestore, useDoc } from '@/firebase';

type UserContextType = {
  user: (Employee & { uid: string }) | null;
  loading: boolean;
};

const UserContext = createContext<UserContextType | undefined>(undefined);

export function UserProvider({ children }: { children: ReactNode }) {
  const { data: authUser, loading: authLoading } = useFirebaseAuthUser();
  const firestore = useFirestore();

  const userDocRef = useMemo(() => {
    if (!firestore || !authUser) return null;
    return doc(firestore, 'users', authUser.uid);
  }, [firestore, authUser]);

  const { data: userProfile, loading: profileLoading } = useDoc(userDocRef);

  const user = useMemo(() => {
    if (!authUser || !userProfile) return null;
    return {
      ...userProfile,
      uid: authUser.uid,
      id: authUser.uid,
      name: userProfile.name || authUser.displayName || 'No Name',
      email: userProfile.email || authUser.email || '',
      avatar: userProfile.avatar || '1',
    } as Employee & { uid: string };
  }, [authUser, userProfile]);

  const loading = authLoading || profileLoading;

  const contextValue = useMemo(() => ({
    user,
    loading,
  }), [user, loading]);

  return (
    <UserContext.Provider value={contextValue}>
      {children}
    </UserContext.Provider>
  );
}

export function useUser() {
  const context = useContext(UserContext);
  if (context === undefined) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
}
